body {
  min-width: 300px;
  color: white;
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body:after {
  content: "";
  position: fixed;
  z-index: -1;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: -webkit-linear-gradient(315deg, #EEA031 18%, #930560 67%);
  background: linear-gradient(135deg, #EEA031 18%, #930560 67%);
}
header {
  display: flex;
  align-items: center;
  margin: 1.5rem 0;
  height: 7rem;
}

header #logo {
  margin: 0 auto;
  display: block;
  height: 100%;
}

header #logo {
  width: 5rem;
  top: 2rem;
  left: 2rem;
}

main {
  display: flex;
}

main[aria-busy="true"] {
  color: white;
  background: #151e1f;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100vw;
  height: 100vh;
}

main[aria-busy="true"] #logo {
  width: 6rem;
  margin-bottom: 1.5rem;
}

nav {
  flex: 1 1;
  min-height: 100vh;
}

ol {
  margin: 0;
  padding: 0 1rem;
  list-style: none;
}

section {
  position: relative;
  align-items: center;
  flex: 1 1;
}

section h1 {
  position: fixed;
  margin: 0;
  left: 2rem;
  bottom: 2rem;
}

@media (max-width: 60rem) {
  section h1 {
    -webkit-transform-origin: left;
            transform-origin: left;
    -webkit-transform: rotate(-90deg);
            transform: rotate(-90deg);
    font-size: 1.25em;
    left: 1.25rem;
    bottom: 0;
  }
}

#guitar {
  height: 100%;
  position: fixed;
  top: 0;
  left: 25%;
  -webkit-transform: translate(-50%);
          transform: translate(-50%);
  display: block;
}

button {
  outline: 0;
  cursor: pointer;
  border-radius: 2rem;
  width: 100%;
  font-size: 1em;
  border: 0;
  background: transparent;
  color: white;
  margin: .15rem 0;
  padding: 0.5rem 0;
  -webkit-transition: 0.5s;
  transition: 0.5s;
}
button:focus,
button:hover {
  background: white;
  color: #151e1f;
}

button:focus {
  box-shadow: 0px 0px 50px rgba(0, 0, 0, 0.5);
}

.loader {
  margin: 0 auto;
  border: 0.25rem solid #2f3333;
  border-top: 0.25rem solid white;
  border-radius: 50%;
  width: 1.5rem;
  height: 1.5rem;
  -webkit-animation: spin 1s linear infinite;
          animation: spin 1s linear infinite;
}

@-webkit-keyframes spin {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes spin {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
